// header
.header {
    &_wrapper {
        width: 100%;
        z-index: 1000;
        header {
            position: relative;
            padding: 0;
            z-index: 1;
            width: 100%;
            .container {
                padding: 12px 12px 0px 12px; } } } }
.column {
    &_brand,
    &_nav {
        display: inline-block;
        vertical-align: bottom;
        padding: 12px 5px 0px;
        text-align: right; }
    &_brand {
        width: 20%;
        padding-bottom: 12px; }
    &_nav {
        width: 75%; } }
nav {
    .nav {
        &--site {
            position: fixed;
            top: -22px;
            left: 0;
            width: 100%;
            height: 150vh;
            padding-top: 75px;
            padding-left: 0px;
            padding-bottom: 50px;
            text-align: center;
            z-index: 9999;
            background-color: transparent;
            transform: translateX(-100%);
            transition: all 0.3s ease-in-out; } } }
